<template>
  <div>
    <mdb-card class="card-image mt-5 pt-5 main">
      <div class="text-white text-center py-5 px-4 my-5">
        <div>
          <h2 class="card-title h1-responsive pt-3 mb-5 font-bold">
            <strong>Todos los productos</strong>
          </h2>
          <p class="mx-5 mb-5">
            {{ categoria }}
          </p>
        </div>
      </div>
    </mdb-card>
    <div class="container mt-5">
      <div>
        <mdb-breadcrumb class="white">
          <mdb-breadcrumb-item
            ><router-link to="/" class="teal-text"
              >Inicio</router-link
            ></mdb-breadcrumb-item
          >
          <mdb-breadcrumb-item v-if="categoria"
            ><a class="teal-text" @click="reload"
              >Servicios</a
            ></mdb-breadcrumb-item
          >
          <mdb-breadcrumb-item v-else active>Servicios</mdb-breadcrumb-item>
          <mdb-breadcrumb-item active>{{ categoria }}</mdb-breadcrumb-item>
        </mdb-breadcrumb>
      </div>
      
      <mdb-card style="background:#2e3951;" class="white-text mb-3">
        <div class="container py-2 px-3">
          <h5 class="font-weight-bold mt-2">Filtrar por categorias:</h5>
        <div class="row mt-3 mb-3">
          <div class="col-md-4">
            <mdb-input type="checkbox" id="checkbox1" name="check1" v-model="circuito" label="Circuito Cerrado de Video" />
          </div>
          <div class="col-md-4">
            <mdb-input type="checkbox" id="checkbox2" name="check2" v-model="sistemas" label="Sistemas de Alarma" />
          </div>
          <div class="col-md-4">
            <mdb-input type="checkbox" id="checkbox3" name="check3" v-model="cercas" label="Cercas Electrificadas" />
          </div>
          <div class="col-md-4">
            <mdb-input type="checkbox" id="checkbox4" name="check4" v-model="control" label="Control de Acceso" />
          </div>
          <div class="col-md-4">
            <mdb-input type="checkbox" id="checkbox5" name="check5" v-model="puertas" label="Puertas Autom√°ticas" />
          </div>
          <div class="col-md-4 mt-2 mx-auto" v-if="categoria">
            <a @click="reload"><small><p>x Eliminar filtros</p></small></a>
          </div>
        </div>
        </div>
      </mdb-card>
      <div class="row pb-5">
        <div
          class="col-md-4 my-3 mx-auto"
          v-for="item in products"
          :key="item.ID"
        >
          <router-link :to="`/product/${item.ID}`"
            ><item-card
              :title="item.Nombre"
              :image="item.imagenes"
              :description="slice(item.descripcionCorta)"
              :categoria="item.categorias"
              class="mx-auto"
              data-aos="zoom-in-up"
            />
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mdbCard, mdbBreadcrumb, mdbBreadcrumbItem, mdbInput } from "mdbvue";
import ItemCard from "../components/ItemCard";

export default {
  components: {
    mdbCard,
    ItemCard,
    mdbBreadcrumb,
    mdbBreadcrumbItem,
    mdbInput
  },
  data() {
    return {
      basicOptions: [
        { text: "Todos los producos", value: "" },
        {
          text: "Circuito Cerrado de Video",
          value: "Circuito Cerrado de Video",
        },
        { text: "Sistemas de Alarma", value: "Sistemas de Alarma" },
        { text: "Cercas Electrificadas", value: "Cercas Electrificadas" },
        { text: "Control de Acceso", value: "Control de Acceso" },
        { text: "Puertas Autom√°ticas", value: "Puertas Autom√°ticas" },
      ],
      categoria: "",
      products: [...this.items],
      circuito: false,
      sistemas: false,
      cercas: false,
      control: false,
      puertas: false,
    };
  },
  computed: {
    selectedData() {
      let response = this.basicOptions.find((option) => {
        return option.selected === true;
      });
      if (response) {
        this.changeCategory(response.value);
        return response.value;
      }
      return "";
    },
  },
  methods: {
    changeCategory(nueva) {
      this.categoria = nueva;
    },
    reload() {
      this.categoria = "";
    },
    slice(text) {
      let sliced = text.slice(0, 70) + "...";
      return sliced;
    },
  },
  props: {
    items: Array,
  },
  watch: {
    categoria(nueva) {
      if (nueva != "") {
        let temp = [...this.items];
        let changes = temp.filter((item) => item.categorias == nueva);
        this.products = changes;
      } else {
        this.products = [...this.items];
      }
    },
    circuito(bool) {
      if (bool) {
        this.categoria = 'Circuito Cerrado de Video'
        this.sistemas = false;
        this.cercas = false;
        this.control = false;
        this.puertas = false;
      }
    },
    sistemas(bool) {
      if (bool) {
        this.categoria = 'Sistemas de Alarma'
        this.circuito = false;
        this.cercas = false;
        this.control = false;
        this.puertas = false;
      }
    },
    cercas(bool) {
      if (bool) {
        this.categoria = 'Cercas Electrificadas'
        this.circuito = false;
        this.sistemas = false;
        this.control = false;
        this.puertas = false;
      }
    },
    control(bool) {
      if (bool) {
        this.categoria = 'Control de Acceso'
        this.circuito = false;
        this.sistemas = false;
        this.cercas = false;
        this.puertas = false;
      }
    },
    puertas(bool) {
      if (bool) {
        this.categoria = 'Puertas Autom√°ticas'
        this.circuito = false;
        this.sistemas = false;
        this.cercas = false;
        this.control = false;
      }
    },
  },
  mounted() {
    if (this.$route.params.category) {
      this.categoria = this.$route.params.category;
      let temp = [...this.items];
      let changes = temp.filter(
        (item) => item.category == this.$route.params.category
      );
      this.products = changes;
    }
  },
};
</script>

<style>
.main {
  background: no-repeat center fixed;
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url("../assets/seguridad/1.jpg");
  color: #e4e4e4;
}
</style>